export default state => ({
    player: state.player,
    game: state.game,
    score: state.score,
});

export const cardsSelector = state => ({
    cards: state.cards,
    removedCardsCount: state.cards.filter(card => !card.get('isRemoved')).size,
    openedCardsCount: state.cards.filter(card => card.get('isOpened')).size,
    openedCards: state.cards.filter(card => card.get('isOpened')),
});



