import {Map} from 'immutable';

import {
    FETCH_SCORE,
    FETCH_SCORE_SUCCESS,
    FETCH_SCORE_ERROR,
} from '../constants/actionTypes';

const initialState = Map({
    scores: [],
    error: '',
    isLoading: false,
});

export default (state = initialState, action) => {
    switch (action.type) {
        case FETCH_SCORE:
            return state.set('isLoading', true);
        case FETCH_SCORE_SUCCESS:
            const scores = action.payload;
            return state.set('scores', scores)
                .set('isLoading', false);
        case FETCH_SCORE_ERROR:
            const {error} = action;
            return state.set('error', error)
                .set('isLoading', false);
        default:
            return state;
    }
}



