import shuffle from 'lodash/shuffle';
import {List, Map} from 'immutable';

import {
    CLOSE_ALL_CARDS,
    GENERATE_CARDS,
    OPEN_CARD,
    REMOVE_CARD,
} from '../constants/actionTypes';

let idCounter = 1;

export default (state = List(), action) => {
    switch (action.type) {
        case GENERATE_CARDS:
            const {cardsCount, backSide, frontImgDir} = action.payload;

            return List(shuffle(Array(cardsCount / 2)
                .fill(null)
                .reduce((acc, cur, idx) => {
                    const card1 = {
                        id: idCounter++,
                        frontSide: `${frontImgDir}/${idx}.jpg`,
                        backSide,
                        isOpened: false,
                        isRemoved: false,
                    };

                    const card2 = {...card1, id: idCounter++};

                    acc.push(Map(card1));
                    acc.push(Map(card2));

                    return acc;
                }, []))
            );
        case OPEN_CARD:
            return state.map(card => {
                if (card.get('id') === action.payload) {
                    return card.update('isOpened', () => true);
                }
                return card;
            });
        case REMOVE_CARD:
            return state.map(card => {
                if (card.get('id') === action.payload) {
                    return card.update('isRemoved', () => true);
                }
                return card;
            });
        case CLOSE_ALL_CARDS:
            return state.map(card => card.update('isOpened', () => false));
        default:
            return state;
    }
}



