import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {withRouter} from 'react-router-dom';
import axios from 'axios';

import './Game.css';
import Board from '../Board/Board';
import Timer from '../Timer/Timer';
import {API_URL} from '../../constants';
import {addResult} from '../../actions/gameActions';
import selectors from '../../selectors';

class Game extends Component {
    componentDidMount() {
        this.timer.start();
    }

    timerRef = timer => {
        this.timer = timer;
    };

    boardRef = board => {
        this.board = board;
    };

    handleGameFinish = async () => {
        this.timer.stop();
        const score = this.timer.getTime();

        this.props.addResult(score);

        const tryAgain = window.confirm(`
            Your time: ${score} seconds
            Try again?
        `);

        if (tryAgain) {
            this.board.getWrappedInstance().generateCards();
            this.timer.start();
        } else {
            const username = this.props.player.get('username');
            const email = this.props.player.get('email');
            const bestScore = Math.min(...this.props.game.get('results'));

            alert(`Best result: ${bestScore} seconds. Your result will be saved on the server`);
            await this.saveScore({username, email, score: bestScore});

            this.props.history.push('/score');
        }
    };

    async saveScore(payload) {
        try {
            await axios.post(API_URL, payload);
        } catch (err) {
            console.log(err);
        }
    }

    render() {
        const cardsCount = this.props.game.get('cardsCount');
        const backSide = this.props.game.get('backSide');

        return (
            <div className="game-container">
                <h1>Match-Match Game</h1>
                <Timer ref={this.timerRef}/>
                <div className="container">
                    <Board
                        ref={this.boardRef}
                        cardsCount={cardsCount}
                        backSide={backSide}
                        onGameFinish={this.handleGameFinish}
                    />
                </div>
            </div>
        );
    }
}

Game.propTypes = {
    game: PropTypes.object.isRequired,
};

export default connect(
    selectors,
    {
        addResult
    }
)(withRouter(Game));



