import React, {Component, Fragment} from 'react';
import PropTypes from 'prop-types';
import sortBy from 'lodash/sortBy';

import './Score.css';
import ScoreItem from '../ScoreItem/ScoreItem';
import LoadingIndicator from '../LoadingIndicator/LoadingIndicator';
import connect from 'react-redux/es/connect/connect';
import {fetchScore} from '../../actions/scoreActions';
import selectors from '../../selectors';

class Score extends Component {
    async componentDidMount() {
        this.props.fetchScore();
    }

    getTop(score, count) {
        return sortBy(score, 'score').slice(0, count);
    }

    render() {
        const isLoading = this.props.score.get('isLoading');
        const scores = this.props.score.get('scores');
        const error = this.props.score.get('error');

        const {top} = this.props;

        const topScore = this.getTop(scores, top);

        if (isLoading) {
            return <LoadingIndicator/>;
        }

        if (!error) {
            return (
                <Fragment>
                    <h2>Top {top} players</h2>
                    <table className="score">
                        <thead>
                        <tr>
                            <th>#</th>
                            <th>Username</th>
                            <th>Email</th>
                            <th>Score</th>
                        </tr>
                        </thead>
                        <tbody>
                        {topScore.map((item, idx) => {
                                item.idx = idx + 1;
                                return <ScoreItem key={item._id} score={item}/>;
                            }
                        )}
                        </tbody>
                    </table>
                </Fragment>
            );
        }
        return <div>{error}</div>;
    }
}

export default connect(
    selectors,
    {
        fetchScore,
    }
)(Score);


Score.propTypes = {
    top: PropTypes.number.isRequired,

    fetchScore: PropTypes.func.isRequired,
    score: PropTypes.object.isRequired,
};

Score.defaultProps = {
    top: 10,
};



