import {
    SET_CARDS_BACK,
    SET_CARDS_COUNT,
    ADD_RESULT,
} from '../constants/actionTypes';

export const setCardsBack = payload => ({
    type: SET_CARDS_BACK,
    payload,
});

export const setCardsCount = payload => ({
    type: SET_CARDS_COUNT,
    payload,
});

export const addResult = payload => ({
    type: ADD_RESULT,
    payload,
});



