import {Map, List} from 'immutable';

import {DIFFICULTY, BACK_IMGS} from '../constants';
import {
    SET_CARDS_BACK,
    SET_CARDS_COUNT,
    ADD_RESULT,
} from '../constants/actionTypes';

const initialState = Map({
    cardsCount: DIFFICULTY.LOW,
    backSide: BACK_IMGS[0],
    results: List(),
});

export default (state = initialState, action) => {
    switch (action.type) {
        case SET_CARDS_BACK:
            const backSide = action.payload;
            return state.set('backSide', backSide);
        case SET_CARDS_COUNT:
            const cardsCount = Number(action.payload);
            return state.set('cardsCount', cardsCount);
        case ADD_RESULT:
            const result = Number(action.payload);
            return state.set('results', state.get('results').push(result));
        default:
            return state;
    }
}



