import axios from 'axios';

import {API_URL} from '../constants';
import {
    FETCH_SCORE,
    FETCH_SCORE_SUCCESS,
    FETCH_SCORE_ERROR,
} from '../constants/actionTypes';

export const fetchScoreSuccess = payload => ({
    type: FETCH_SCORE_SUCCESS,
    payload,
});

export const fetchScoreError = payload => ({
    type: FETCH_SCORE_ERROR,
    error: payload,
});

export const fetchScore = () => {
    return async dispatch => {
        dispatch({type: FETCH_SCORE});

        try {
            const res = await axios.get(API_URL);
            const scores = res.data.result;

            dispatch(fetchScoreSuccess(scores));
        } catch (err) {
            dispatch(fetchScoreError(err.message));
        }
    }
};



