import React from 'react';

import './Welcome.css';
import Help from '../Help/Help';
import SettingsForm from '../SettingsForm/SettingsForm';

const Welcome = () => {
    return (
        <section className="welcome">
            <Help/>
            <SettingsForm/>
        </section>
    );
};

export default Welcome;



