import React from 'react';
import PropTypes from 'prop-types';

import './Card.css';

const Card = ({isOpened, isRemoved, onCardClick, backSide, frontSide}) => {
    const openCard = isOpened ? ' open-card' : '';
    const removeCard = isRemoved ? ' remove-card' : '';

    return (
        <div className="card-container">
            <figure className={'card' + openCard + removeCard} onClick={onCardClick}>
                <img className="back" src={backSide} alt='' />
                <img className="front" src={frontSide} alt='' />
            </figure>
        </div>
    );
};

Card.propTypes = {
    frontSide: PropTypes.string.isRequired,
    backSide: PropTypes.string.isRequired,
    isOpened: PropTypes.bool.isRequired,
    isRemoved: PropTypes.bool.isRequired,
    onCardClick: PropTypes.func.isRequired,
};

export default Card;



