import {
    GENERATE_CARDS,
    OPEN_CARD,
    REMOVE_CARD,
    CLOSE_ALL_CARDS,
} from '../constants/actionTypes';

export const generateCards = payload => ({
    type: GENERATE_CARDS,
    payload,
});

export const openCard = payload => ({
    type: OPEN_CARD,
    payload,
});

export const removeCard = payload => ({
    type: REMOVE_CARD,
    payload,
});

export const closeAllCards = () => ({
    type: CLOSE_ALL_CARDS,
});



