import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {withRouter} from 'react-router-dom';

import './SettingsForm.css';
import {BACK_IMGS, DIFFICULTY} from '../../constants';
import selectors from '../../selectors';
import {setCardsBack, setCardsCount} from '../../actions/gameActions';
import {
    setPlayerUsername,
    setPlayerEmail,
} from '../../actions/playerActions';

class SettingsForm extends Component {
    handleSubmit = evt => {
        evt.preventDefault();
        this.props.history.push('/game');
    };

    handleUsernameChange = evt => {
        this.props.setPlayerUsername(evt.target.value);
    };

    handleEmailChange = evt => {
        this.props.setPlayerEmail(evt.target.value);
    };

    handleCardsCountChange = evt => {
        this.props.setCardsCount(evt.target.value);
    };

    handleBackSideChange = evt => {
        this.props.setCardsBack(evt.target.value);
    };

    render() {
        return (
            <form className="settings" onSubmit={this.handleSubmit}>
                <fieldset>
                    <legend>Player profile</legend>
                    <label>
                        Username *
                        <input type="text" name="username"
                               value={this.props.player.username}
                               onChange={this.handleUsernameChange}
                               required
                               autoFocus
                        />
                    </label>
                    <label>
                        Email *
                        <input type="email" name="email"
                               value={this.props.player.email}
                               onChange={this.handleEmailChange}
                               required
                        />
                    </label>
                </fieldset>

                <fieldset>
                    <legend>Cards back</legend>

                    {BACK_IMGS.map((backImg, idx) =>
                        <label key={backImg}>
                            <input type="radio" name="back"
                                   value={backImg}
                                   checked={this.props.game.get('backSide') === BACK_IMGS[idx]}
                                   onChange={this.handleBackSideChange}
                            />
                            <img src={backImg} height="100" width="100" alt=""/>
                        </label>
                    )}
                </fieldset>

                <fieldset>
                    <legend>Game difficulty</legend>
                    <label>
                        <input type="radio" name="cardsCount"
                               value={DIFFICULTY.LOW}
                               checked={this.props.game.get('cardsCount') === DIFFICULTY.LOW}
                               onChange={this.handleCardsCountChange}
                        />
                        Low
                    </label>

                    <label>
                        <input type="radio" name="cardsCount"
                               value={DIFFICULTY.MEDIUM}
                               checked={this.props.game.get('cardsCount') === DIFFICULTY.MEDIUM}
                               onChange={this.handleCardsCountChange}
                        />
                        Medium
                    </label>
                    <label>
                        <input type="radio" name="cardsCount"
                               value={DIFFICULTY.HIGH}
                               checked={this.props.game.get('cardsCount') === DIFFICULTY.HIGH}
                               onChange={this.handleCardsCountChange}
                        />
                        High
                    </label>
                </fieldset>

                <button className="start-btn">Start</button>
            </form>
        );
    }
}

SettingsForm.propTypes = {
    setPlayerUsername: PropTypes.func.isRequired,
    setCardsBack: PropTypes.func.isRequired,
    setCardsCount: PropTypes.func.isRequired,
    setPlayerEmail: PropTypes.func.isRequired,
    player: PropTypes.object.isRequired,
    game: PropTypes.object.isRequired,
};

export default connect(
    selectors,
    {
        setPlayerUsername,
        setCardsBack,
        setCardsCount,
        setPlayerEmail,
    }
)(withRouter(SettingsForm));



