import {
    SET_PLAYER_USERNAME,
    SET_PLAYER_EMAIL,
} from '../constants/actionTypes';

export const setPlayerUsername = payload => ({
    type: SET_PLAYER_USERNAME,
    payload,
});

export const setPlayerEmail = payload => ({
    type: SET_PLAYER_EMAIL,
    payload,
});



