import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';

import './Board.css';
import Card from '../Card/Card';
import {FRONT_IMG_DIR} from '../../constants';
import {generateCards, openCard, removeCard, closeAllCards} from '../../actions/cardsActions';
import {cardsSelector} from '../../selectors';

class Board extends Component {
    handleClick = card => {
        const {openedCards, openedCardsCount} = this.props;

        if (openedCardsCount === 1) {
            setTimeout(() => this.compareCards(openedCards.get(0), card), 500);
        }

        this.props.openCard(card.get('id'));
    };

    compareCards(card1, card2) {
        if (card1.get('frontSide') === card2.get('frontSide')) {
            this.props.removeCard(card1.get('id'));
            this.props.removeCard(card2.get('id'));
        }

        this.props.closeAllCards();

        if (this.isGameFinished()) {
            this.props.onGameFinish();
        }
    }

    isGameFinished() {
        return this.props.removedCardsCount === 0;
    }

    componentDidMount() {
        this.generateCards();
    }

    generateCards() {
        const {cardsCount, backSide} = this.props;

        this.props.generateCards({
            cardsCount,
            backSide,
            frontImgDir: FRONT_IMG_DIR
        });
    }

    render() {
        return (
            <div className="board">
                {this.props.cards.map(card =>
                    <Card
                        key={card.get('id')}
                        onCardClick={this.handleClick.bind(this, card)}
                        frontSide={card.get('frontSide')}
                        backSide={card.get('backSide')}
                        isOpened={card.get('isOpened')}
                        isRemoved={card.get('isRemoved')}
                    />
                )}
            </div>
        );
    }
}

Board.propTypes = {
    cardsCount: PropTypes.number.isRequired,
    backSide: PropTypes.string.isRequired,
    onGameFinish: PropTypes.func.isRequired,

    cards: PropTypes.object.isRequired,
    generateCards: PropTypes.func.isRequired,
    openCard: PropTypes.func.isRequired,
    closeAllCards: PropTypes.func.isRequired,
    removeCard: PropTypes.func.isRequired,
};

Board.defaultProps = {
    cardsCount: 8,
};

export default connect(
    cardsSelector,
    {
        generateCards,
        openCard,
        closeAllCards,
        removeCard,
    },
    null,
    {withRef: true},
)(Board);



